<template>
  <div>
    <div id='wrapper'>
      <h1>聚合天气</h1>
      <div id='sw'>
        <input type='text' placeholder='请输入城市' id='search' @change='searchWeather' />
        <button id='btn'>Search</button>
      </div>
      <ul>
        <li>
          日期：
          <span>{{todayWeather.city + " —— "+todayWeather.date_y + " —— "+todayWeather.week}}</span>
        </li>
        <li>
          气温：
          <span>{{todayWeather.temperature}}</span>
        </li>
        <li>
          风向：
          <span>{{todayWeather.wind}}</span>
        </li>
        <li>
          天气状况：
          <span>{{todayWeather.weather}}</span>
        </li>
        <li>
          穿衣：
          <span style='font-size:12px'>{{todayWeather.dressing_advice}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      baseUrl: "https://v.juhe.cn/",
      url: null,
      cityName: "贵阳",
      citynameutf: null,
      todayWeather: {}
    };
  },
  created() {
    this.setUrl();
    this.getWeather(this.url);
  },
  methods: {
    async getWeather(url) {
      const { data: res } = await this.axios.get(url);
      this.todayWeather = res.result.today;
      console.log(res);
    },
    setUrl() {
      console.log(this.baseUrl);
      this.citynameutf = encodeURI(this.cityName);
      console.log(this.citynameutf);
      this.url = `${this.baseUrl}/api/weather/index?format=1&cityname=${this.citynameutf}&key=0ee92ff7f36b9b6ac657b285edb11772`;
    },
    searchWeather(e) {
      console.log(e);
      this.cityName = e.target.value.trim();
      this.setUrl();
      this.getWeather(this.url);
    }
  }
};
</script>

